@import "_partial/_config";

/* background */
.infoFaq,
.infoContact {
	background: url(/img/img01.jpg) no-repeat center center;
	background-size: cover;
	background-attachment: fixed;
}

/* -----------------------------------------------------------------------
## faq ###################################################################
----------------------------------------------------------------------- */
.faq {
	padding-top: 16px;
	&_categoryList {
		margin-top: 32px;
		display: flex;
	}
	.categoryList {
		margin-left: -32px;
		&_item {
			flex: 1;
			text-align: center;
			margin-left: 32px;
		}
		a {
			display: block;
			padding: 8px 0;
			border: 2px solid $color_line;
		}
	}
	.qaBox {
		margin: 32px 0 64px;
		padding-top: 8px;
		&:last-of-type {
			margin-bottom: 0;
		}
		&_title {
			@include fontsize(24);
			text-align: center;
			text-decoration: underline;
		}
		&_list {
			@include fontsize(14)
			&_q {
				float: left;
				clear: both;
				width: 50%;
				margin-top: 32px;
				padding: 16px 16px 0 0;
				border-top: 1px solid $color_line;
			}
			&_a {
				float: right;
				width: 50%;
				margin-top: 32px;
				padding: 16px 0 0 16px;
				border-top: 1px solid $color_line;
			}
		}
	}
}
@include mediaQuery(sp){
	.faq {
		&_categoryList {
			margin-top: 16px;
		}
		.categoryList {
			margin-left: -8px;
			&_item {
				margin-left: 8px;
			}
		}
		.qaBox {
			margin: 16px 0;
			&_list {
				@include fontsize(14)
				&_q {
					float: none;
					width: 100%;
					margin-top: 32px;
					padding: 16px 0 0 0;
				}
				&_a {
					float: none;
					width: 100%;
					margin-top: 0;
					padding-left: 0;
					border-top: none;
				}
			}
		}
	}
}



/* -----------------------------------------------------------------------
## contact ###############################################################
----------------------------------------------------------------------- */
.contacts {
	padding-top: 16px;
	text-align: center;
	&_counter {
		margin-top: 64px;
	}
	&_tel {
		@include fontsize(40);
		font-weight: bold;
	}
	&_add {
		margin-top: 64px;
	}
	&_means {
		margin-top: 32px;
	}
	.sp {
		display: none;
	}
}

@include mediaQuery(sp){
	.contacts {
		&_counter {
			margin-top: 32px;
		}
		&_tel {
			@include fontsize(32);
		}
		&_add {
			margin-top: 32px;
			@include fontsize(14);
		}
		&_means {
			margin-top: 16px;
			@include fontsize(14);
		}
		.sp {
			display: inline;
		}
	}
}